<template>
<div>
	<div class="tableSelect">
		<select id="required" @change="">
			<option value="1" disabled selected>选择年制(必选项)</option>
			<option value="2">五年制</option>
			<option value="3">三年制</option>
		</select>
		<select>
			<option value="1" disabled selected>选择学期</option>
			<option value="2">2016-2017第一学期</option>
			<option value="3">2016-2017第二学期</option>
		</select>
		<select id="required" @click="">
			<option value="1" disabled selected>选择课程(必选项)</option>
			<option value="2">护理学</option>
			<option value="3">护理学</option>
		</select>
		<select id="required">
			<option value="1" disabled selected>选择班级(必选项)</option>
			<option value="2">一班</option>
			<option value="3">二班</option>
		</select>
		<span class="inputNumber">
			<input v-model="inputNum" id="inputNum" placeholder="输入学号（必选填）">
		</span>
		<button class="am-btn am-btn-success am-radius">查询</button>
		<button class="am-btn am-btn-success am-radius" v-on:click="exportBtn">下载</button>
	</div>

	<div id="gradeContent">
		<div class="gradeContentBody">
			<table class="normalTable">
				<thead>
					<tr>
						<th>学号</th>
						<th>姓名</th>
						<th>年级</th>
						<th>专业</th>
						<th>学期</th>
						<th>课程</th>
						<th>分数</th>
					</tr>
				</thead>
				<tbody>
					<tr v-for="data in gradeStat">
						<td v-text="data.stuNum"></td>
						<td v-text="data.stuName"></td>
						<td v-text="data.stuGrade"></td>
						<td v-text="data.stuMajor"></td>
						<td v-text="data.stuSemester"></td>
						<td v-text="data.stuCourse"></td>
						<td v-text="data.stuScore"></td>
					</tr>
				</tbody>
			</table>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'gradeContent',
	data () {
		return {
			gradeStat: [
				{stuNum: '20142201010', stuName: '何平', stuGrade: '大二', stuMajor: '护理学', stuSemester: '2016-2017第一学期', stuCourse: '护理学', stuScore: '80'},
				{stuNum: '20142201010', stuName: '何平', stuGrade: '大二', stuMajor: '护理学', stuSemester: '2016-2017第一学期', stuCourse: '护理学', stuScore: '80'},
				{stuNum: '20142201010', stuName: '何平', stuGrade: '大二', stuMajor: '护理学', stuSemester: '2016-2017第一学期', stuCourse: '护理学', stuScore: '80'}
			]
		}
	},
	beforeMount: function () {
		this.$http.post('../eduGradeQuery.php').then(function (response) {
			console.log(response);
			this.gradeStat = response.body.gradeStat;
		});
	}
}
</script>

<style>
#gradeContent {
	background-color: #f3f3f3;
	width: 100%;
	text-align: center;
}
.gradeContentBody {
	padding: 1rem 5rem;
}
/*.gradeContentBody table {
	margin-bottom: 1.6rem;
}*/

</style>



