<template>
<div>
	<div class="blank">
		<div class="leftPart">
			<span>当前学年学期：{{preSemester}}</span>
			<span>当前周数：
				<span class="textBlue">{{preWeek}}</span>
			</span>
		</div>
		<div class="rightPart">
			<span>温馨提示：</span>
			<span class="textBlue">{{warmPrompt}}</span>
		</div>
	</div>

	<div id="tchGrdPutBody">
		<div class="tchGrdPutDiv">
			<!-- <span>何老师，你在【2016-2017学年第一学期】共上【4】门课</span> -->
			<div class="tchGpMain">
				<div class="tchGrdTable">
					<table class="operationTable">
						<thead>
							<tr>
								<th width="7%">输入</th>
								<th width="5%">序号</th>
								<th width="12%">学年学期</th>
								<th width="11%">教师</th>
								<th width="34%">班级名称</th>
								<th width="10%">课程名称</th>
								<th width="9%">导出成绩</th>
							</tr>
						</thead>
						<tbody>
							<tr v-for="data in tchGradesList">
								<td class="textBtn"><a @click="gradeInputBtn">{{data.gradeInput}}</a></td>
								<td v-text="data.number"></td>
								<td v-text="data.yearSemester"></td>
								<td v-text="data.teacher"></td>
								<td v-text="data.className"></td>
								<td v-text="data.courseName"></td>
								<td class="textBtn"><a @click="exportFormatBtn">下载</a></td>
							</tr>
						</tbody>
					</table>
				</div>
			</div>
		</div>
	</div>
</div>
</template>

<script>
export default {
	name: 'tchGrdPutBody',
	data () {
		return {
			preSemester: '2016-2017年第一学期',
			preWeek: '20',
			warmPrompt: '按排课信息输入成绩（若单科成绩输入时间长，请15分钟保持一次）',

			tchGradesList: [
				{gradeInput: '成绩输入', number: '1', yearSemester: '2016-2017学年第一学期', teacher: '何平（61742）', className: '对口高职2015护理（9+3）1班+普通高中2015护理2班护理管理学多班拆分A班', courseName: '护理管理学'},
				{gradeInput: '成绩输入', number: '1', yearSemester: '2016-2017学年第一学期', teacher: '何平（61742）', className: '对口高职2015护理（9+3）1班+普通高中2015护理2班护理管理学多班拆分A班', courseName: '护理管理学'},
				{gradeInput: '成绩输入', number: '1', yearSemester: '2016-2017学年第一学期', teacher: '何平（61742）', className: '对口高职2015护理（9+3）1班+普通高中2015护理2班护理管理学多班拆分A班', courseName: '护理管理学'},
				{gradeInput: '成绩输入', number: '1', yearSemester: '2016-2017学年第一学期', teacher: '何平（61742）', className: '对口高职2015护理（9+3）1班+普通高中2015护理2班护理管理学多班拆分A班', courseName: '护理管理学'}
			]
		}
	},
	beforeMount: function () {
		this.$http.post('../tchGradesInput.php').then(function (response) {
			console.log(response);
			this.preSemester = response.body.preSemester;
			this.preWeek = response.body.preWeek;
			this.tchGradesList = response.body.tchGradesList;
		});
	},
	methods: {
		gradeInputBtn: function () {
			window.location.href = "/teacher/gradesInput";
		},
		exportFormatBtn: function () {
			var r = confirm("您确定将文件导出格式吗？");
			if (r==true) {
				alert("导出成功！");
			}
			else {
				alert("导出失败。");
			}
		}
	}
}
</script>

<style>
#tchGrdPutBody {
	background-color: #f3f3f3;
}
.tchGrdPutDiv {
	padding: 0.6rem 0;
	text-align: center;
	font-size: 1rem;
}
.tchGpMain {
	text-align: center;
	background-color: white;
	margin: 0.5rem 5rem;
	padding-bottom: 1.3rem;
	border: 1px solid #ececec;	/*新加*/
}
.tchGrdTable {
	margin: 0.5rem 0.5rem;
	/*height: 20rem;*/
	border: 0.7rem solid transparent;
}
.tchGrdTable table {
	border-collapse: collapse;
	/*height: 3.2rem;*/
}

/*.textBtn a {
	text-decoration: underline;
	color: #3399ff;
	cursor: pointer;
}
.textBtn a:hover,
.textBtn a:focus {
	color: #5cadff;
}
.textBtn a:active {
	color: #3091f2;
}*/
</style>


